import {HttpClient} from "@angular/common/http";import {Injectable} from "@angular/core";import {Observable} from "rxjs/Observable";import {SERVER_API_URL} from "../../app/app.constants";import {HTTP} from "@ionic-native/http";@Injectable()export class LoginService {    constructor(private http: HttpClient,private nativeHttp:HTTP) {    }    loginpost(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/eaccount/Login', data)    }    loginpostByNative(data){        return this.nativeHttp.post(SERVER_API_URL + '/eaccount/Login',data,{'Content-Type': `application/json`});    }}


