import {HttpClient} from "@angular/common/http";import {Injectable} from "@angular/core";import {Observable} from "rxjs/Observable";import {SERVER_API_URL} from "../../app/app.constants";import {HTTP} from "@ionic-native/http";@Injectable()export class HomeService {    constructor(private http: HttpClient,private nativeHttp:HTTP) {    }    //获取banner    getBannerList(): Observable<any> {        return this.http.get(SERVER_API_URL + '/ENews/GetBannerList')    }    //获取banner By Native    getBannerListByNative(){        return this.nativeHttp.get(SERVER_API_URL + '/ENews/GetBannerList',null,null)    }    //优秀教师，关注教师    GetGoodTeacherList(): Observable<any> {        return this.http.get(SERVER_API_URL + '/user/GetGoodTeacherList')    }    GetGoodTeacherListBynative(){        return this.nativeHttp.get(SERVER_API_URL + '/user/GetGoodTeacherList',null,null)    }    //课程分类    GetDictionaryByPCode(params): Observable<any> {        return this.http.get(SERVER_API_URL + '/EProduct/GetEproductCountByCode?code=' + params);    }    GetDictionaryByPCodeByNative(params){        return this.nativeHttp.get(SERVER_API_URL + '/EProduct/GetEproductCountByCode?code=' + params,null,null);    }}


