import { ToastController } from "ionic-angular";import {Injectable} from "@angular/core";@Injectable()export class CommonService {    constructor(public toastCtrl:ToastController) {}    /**     * 提示信息 位置：居中，延时2s     * @param message  提示文字     * @param callback  提示信息之后执行的方法     */    toast(message, callback?) {        let toast = this.toastCtrl.create({            message: message,            duration: 2000,            position: 'middle',            dismissOnPageChange: true,        });        toast.present();        if (callback) {            callback();        }    }}


